import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'

class PlanTourInfo extends Component {

    handleClick = (route)=>{
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
        this.props.history.push(`/${route}`)
    }

    render(){
        return (
            <section id="custom">
            <div id="content">
                <div id="left-content">
                    <h1>Didn't find your <br />
                        Pacakge ? <br />
                        Feel Free To ask us <br />
                        We will make it for you
                    </h1>
                    <h4>Feel Free To Reach Out Our User Friendly Team is always there to you in any Techical Or Non Technical Situation We will Surely love to Help You.<br /><br />
                        Connect with <br />
                        Ms. Snigdha jha (+91 9818040401)  <br />
                        Product Manager   
                    </h4>                
                    <button  onClick={()=> this.handleClick("tours")} className="btn waves-effect btn-large blue darken-4" id="customPacCorona">Request Custom Package</button>
                </div>
                <div id="right-content">
                </div>
            </div>
        </section>
        )
    }
}

export default withRouter(PlanTourInfo);
